'use strict';

import * as d3 from 'd3';

const X_AXIS_CONTAINER_CLASSNAME = 'container-x-axis';

export function renderContainer (chartContainerEle, width, height, heightByData, margin, options) {
  const $chartContainer = d3.select(chartContainerEle);
  const isXAxisFixed = options && options.isXAxisFixed;
  const chartContainerWidth = width + margin.left + margin.right;
  let chartContainerHeight = heightByData + margin.top + margin.bottom;
  const chartWrapperWidth = chartContainerWidth;
  let chartWrapperHeight = chartContainerHeight;
  if (isXAxisFixed === true) {
    chartContainerHeight = heightByData < height ? heightByData + margin.top : height;
    chartWrapperHeight = heightByData + margin.top;
  }

  $chartContainer
    .style('width', `${chartContainerWidth}px`)
    .style('height', `${chartContainerHeight}px`);
  const $chartWrapper = $chartContainer
    .append('svg')
    .attr('width', chartWrapperWidth)
    .attr('height', chartWrapperHeight)
    .style('display', 'block')
    .append('g')
    .attr('transform', `translate(${margin.left}, ${margin.top})`);
  let $xAxisContainer;
  let $xAxisWrapper;
  let xAxisWrapperHeight;
  if (options && options.isXAxisFixed === true) {
    const parentEle = chartContainerEle.parentNode;
    const xAxisContainerEle = document.createElement('div');
    xAxisWrapperHeight = options.hasXAxisLabel === true ? 45 : 30;
    xAxisContainerEle.classList.add(X_AXIS_CONTAINER_CLASSNAME);
    parentEle.insertBefore(xAxisContainerEle, null);
    $xAxisContainer = d3.select(xAxisContainerEle)
      .style('width', `${chartContainerWidth}px`)
      .style('height', `${xAxisWrapperHeight}px`);
    $chartContainer
      .style('overflow-y', 'auto')
      .style('overflow-x', 'hidden');
    $xAxisWrapper = $xAxisContainer
      .append('svg')
      .attr('width', chartContainerWidth)
      .attr('height', xAxisWrapperHeight)
      .append('g')
      .attr('transform', 'translate(' + margin.left + ', 0)');
  }

  return {
    chartContainerWidth: chartContainerWidth,
    chartContainerHeight: chartContainerHeight,
    chartWrapperWidth: chartWrapperWidth,
    chartWrapperHeight: chartWrapperHeight,
    xAxisWrapperHeight: xAxisWrapperHeight,
    $chartContainer: $chartContainer,
    $chartWrapper: $chartWrapper,
    $xAxisContainer: $xAxisContainer,
    $xAxisWrapper: $xAxisWrapper
  };
}

export function removeContainer (chartContainerEle, options = {}) {
  const parentEle = chartContainerEle.parentNode;
  const xAxisContainerEle = parentEle.querySelector(`.${X_AXIS_CONTAINER_CLASSNAME}`);
  if (xAxisContainerEle !== null) { xAxisContainerEle.remove(); }
  chartContainerEle.style.overflow = null;
  for (const children of chartContainerEle.childNodes) {
    children.remove();
  }
}

export default {
  renderContainer: renderContainer,
  removeContainer: removeContainer
};
