'use strict';

// import * as d3 from 'd3';
import { tip as d3Tip } from 'd3-v6-tip';

class Tooltip {
  constructor () {
    this.tip = d3Tip().attr('class', 'd3-tip').html((EVENT, d) => d);
  }

  show (event, data, template) {
    let content = !template ? '<strong>{{title}}</strong><br>{{value}}' : template;
    for (const [key, value] of Object.entries(data)) {
      const token = '{{' + key + '}}';
      content = content.replace(token, value);
    }
    this.tip.show(event, content);
  }

  hide () {
    this.tip.hide();
  }
}

const createTooltip = () => {
  // const tooltip = d3Tip().html((EVENT, d) => d);
  const tooltip = new Tooltip();
  return tooltip;
};

