export const getTooltipContent = function (templateText, data) {
  // const re = /(?<=\[\[).+?(?=\]\])/g; // not supported by Safari
  const re = /(\[\[).+?(?=\]\])/g;
  let result = templateText;
  const matches = templateText.match(re);
  for (const match_ of matches) {
    const match = match_.split('[[')[1];
    const vals = match.split('#');
    if (['internal'].includes(vals[0])) {
      result = result.replace(`[[${match}]]`, data[vals[1]]);
      continue;
    }
    if (['global'].includes(vals[0])) {
      result = result.replace(`[[${match}]]`, this[vals[1]]);
    }
    result = result.replace(`[[${match}]]`, data[vals[0]]);
  }
  return result;
};
export const getTooltipTemplate = (templateText, internalVal, globalVal) => {
  // const re = /(?<=\[\[).+?(?=\]\])/g; // not supported by Safari
  const re = /(\[\[).+?(?=\]\])/g;
  let result = templateText;
  const matches = templateText.match(re);
  for (const match_ of matches) {
    const match = match_.split('[[')[1];
    const vals = match.split('#');
    let iterateVal = '';
    if (vals[0] === 'internal') {
      iterateVal = internalVal;
    } else if (vals[0] === 'global') {
      iterateVal = globalVal;
    }
    if (iterateVal) {
      result = result.replace(`[[${match}]]`, `$\{${iterateVal}.${vals[1]}}`);
    } else {
      result = result.replace(`[[${match}]]`, `$\{${vals[0]}}`);
    }
  }
  return result;
};

export default createTooltip;
